.page-content {
  // .page-content__delivery
  &__delivery {
  }
}

.delivery-page {
  counter-reset : delivery-item;

  // .delivery-page__map
  &__map {
    width         : 100%;
    height        : 600px;
    margin-top    : 30px;
    margin-bottom : 45px;
  }

  // .delivery-page__item
  &__item {
    font-size    : 14px;
    line-height  : 20px;
    position     : relative;
    max-width    : 930px;
    padding-left : 20px;

    &::before {
      content           : counter(delivery-item);
      font-size         : 15px;
      line-height       : 16px;
      counter-increment : delivery-item;
      color             : #ec621f;
      position: absolute;
      font-weight: bold;
      left : 0;

    }
  }


  // .delivery-page__transport
  &__transport {
    margin-top : 30px;
  }
}

.transport-companies {

  // .transport-companies__title
  &__title {
    font-size   : 15px;
    font-weight : bold;

  }

  // .transport-companies__list
  &__list {
  }

  // .transport-companies__table
  &__table {
    font-size       : 13px;
    font-weight     : normal;
    line-height     : 1.23;
    width           : 100%;
    margin-top      : 20px;
    border-collapse : collapse;


    tr {
      border-bottom : 1px solid rgba(0, 0, 0, 0.25);
    }

    th {
      padding : 15px 0;

    }

    td {
      padding : 18px 15px 18px 0;

      &:first-child {
        font-weight : bold;
      }
    }
  }
}
